

*{box-sizing:content-box; outline:none;}
html{  -moz-text-size-adjust:100%; -ms-text-size-adjust:100%; -o-text-size-adjust:100%; -webkit-text-size-adjust:100%; text-size-adjust:100%;}

:root{
     
    font-family: 'Fira Sans', 
    sans-serif; font-weight: 400; 
    font-style: normal; 
    --primary-color: #32428B;
    --text-color: #202020;
    --alt-text-color: #9C9C9C;
    --active-color: #2FD385;
    --bg-color: #E5E5E5;
    --alt-bg-color: #FFFFFF; 
    --decor-color: #DBE5F3;
    --alt-decore-color: #EEEEEE;
    font-size: 17px; 
    line-height: 1.4;
}
 

body{background-color: var(--bg-color); margin:0 auto; padding:0; -webkit-font-smoothing: antialiased; -moz-font-smoothing: antialiased; -o-font-smoothing: antialiased;    position: relative; color: var(--text-color);  }   

h1, h2, h3, h4, h5, h6, p{display:block; margin:0; padding:0; font-weight: 400;}

.h1, .h2, .h3, .h4, .h5, .h6{margin:0; padding:0; float:none;}

img, input, textarea, button, a{border:none; outline:none;}

input, textarea, button, a{font-family: 'Fira Sans', sans-serif; color: var(--text-color); font-weight: normal; font-size: 1.11rem; line-height: 1;}

table{border-spacing:0; border-collapse: collapse; margin: 0; padding: 0;}
td, th{vertical-align:top; word-wrap:break-word;}
ul, ol, nav, dl{margin:0; padding:0; list-style:none;}
a{text-decoration:none;}
a:hover{text-decoration:underline;}
button{display:block; cursor:pointer; box-sizing:border-box; padding:0;}
button, .button{ text-decoration:none !important;}
//button:hover, .button:hover{transition: opacity 1.2s, visibility 0s linear 1.2s; opacity:0.7;}

.inner{position:relative; max-width:1160px; width: 96%; margin:0 auto; box-sizing: border-box; overflow: hidden; } 
.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    clip: rect(0 0 0 0);
    overflow: hidden;
}
figure{
    margin: 0;
}
section{position:relative;}
main{position:relative; clear:both; padding-bottom: 4.44rem; flex-grow: 1;}
header, nav, section, article, aside, footer, main{display:block;}
#wrapper{width:100%; min-height:100vh; margin:0 auto; display: flex; flex-direction: column;}
#footer{width: 100%; box-sizing: border-box; position: relative; margin-top: auto; background-color: grey;}
.cover{background-position:center center; background-repeat:no-repeat; background-size:cover;}
.contain{background-position:center center; background-repeat:no-repeat; background-size:contain;}
.scale{-moz-transition:all 1s ease-out; -o-transition:all 1s ease-out; -webkit-transition:all 1s ease-out; transition:all 1s ease-out;}
.scale:hover{-webkit-transform:scale(1.1); -moz-transform:scale(1.1); -o-transform:scale(1.1); transform: scale(1.1);}
.rot{-webkit-transform: rotate(180deg); -ms-transform: rotate(180deg);  transform: rotate(180deg);}

 

