.inputbox::-webkit-input-placeholder{opacity: 1; transition: opacity 0.3s ease; color:#000000;}
.inputbox::-moz-placeholder{opacity: 1; transition: opacity 0.3s ease; color:#000000;}
.inputbox:-moz-placeholder{opacity: 1; transition: opacity 0.3s ease; color:#000000;}
.inputbox:-ms-input-placeholder{opacity: 1; transition: opacity 0.3s ease; color:#000000;}
.inputbox:focus::-webkit-input-placeholder{opacity: 0; transition: opacity 0.3s ease;}
.inputbox:focus::-moz-placeholder{opacity: 0; transition: opacity 0.3s ease;}
.inputbox:focus:-moz-placeholder{opacity: 0; transition: opacity 0.3s ease;}
.inputbox:focus:-ms-input-placeholder{opacity: 0; transition: opacity 0.3s ease;}

.textarea::-webkit-input-placeholder{opacity: 1; transition: opacity 0.3s ease;}
